<template>
  <div class="wrapper" ref="homeElement">
    <StaticPart/>
    <Nearby/>
  </div>
  <Docker/>
</template>

<script>
import StaticPart from '@/views/home/StaticPart.vue'
import Nearby from '@/views/home/Nearby.vue'
import Docker from '@/views/home/Docker.vue'
import { onActivated, ref } from 'vue'
import { onBeforeRouteLeave } from 'vue-router'

export default {
  name: 'Home',
  components: {
    StaticPart,
    Nearby,
    Docker
  },
  setup () {
    const homeElement = ref(null)
    const scrollTop = ref(0)

    // 记录滚动条位置
    onActivated(() => {
      homeElement.value.scrollTop = scrollTop.value
      scrollTop.value = 0
    })
    onBeforeRouteLeave(() => {
      scrollTop.value = homeElement.value.scrollTop
    })
    return {
      homeElement
    }
  }
}
</script>

<style lang="scss" scoped>
.wrapper {
  position: absolute;
  top: 0;
  bottom: .5rem;
  left: 0;
  right: 0;
  padding: 0 .18rem .18rem .18rem;
  overflow: auto;
}
</style>
